.placeList {
  list-style: none;
  margin: 1rem auto;
  padding: 0;
  width: 90%;
  max-width: 40rem;
}

.item {
  margin: 1rem 0;
}

.content {
  padding: 0;
}

.info {
  padding: 1rem;
  text-align: center;
}

.image {
  width: 100%;
  height: 12.5rem;
  margin-right: 1.5rem;
}

.image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.info h2,
.info h3,
.info p {
  margin: 0 0 0.5rem 0;
}

.actions {
  padding: 1rem;
  text-align: center;
  border-top: 1px solid #ccc;
}

.modalContent {
  padding: 0;
}

.modalActions {
  text-align: right;
}

.actions button,
.actions a {
  margin: 0.5rem;
}

.mapContainer {
  height: 16rem;
  width: 100%;
}

@media (min-width: 768px) {
  .image {
    height: 20rem;
  }
}
