.navLinks {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.navLinks li {
  margin: 1rem;
}

.navLinks a {
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
}

.navLinks a:hover,
.navLinks a:active,
.navLinks a.active {
  background: #f8df00;
  border-color: #292929;
  color: #292929;
}

.navLinks button {
  cursor: pointer;
  border: 1px solid #292929;
  color: #292929;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.navLinks button:focus {
  outline: none;
}

.navLinks button:hover,
.navLinks button:active {
  background: #292929;
  color: white;
}

@media (min-width: 768px) {
  .navLinks {
    flex-direction: row;
  }

  .navLinks li {
    margin: 0 0.5rem;
  }

  .navLinks a {
    color: white;
    text-decoration: none;
  }

  .navLinks button {
    border: 1px solid white;
    color: white;
    background: transparent;
  }

  .navLinks button:hover,
  .navLinks button:active {
    background: #f8df00;
    color: #292929;
  }
}
