.formControl {
  margin: 1rem 0;
}

.formControl label,
.formControl input,
.formControl textarea {
  display: block;
}

.formControl label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.formControl input,
.formControl textarea {
  width: 100%;
  font: inherit;
  border: 1px solid #ccc;
  background: #f8f8f8;
  padding: 0.15rem 0.25rem;
}

.formControl input:focus,
.formControl textarea:focus {
  outline: none;
  background: #ebebeb;
  border-color: #510077;
}

.formControlInvalid label,
.formControlInvalid p {
  color: red;
}

.formControlInvalid input,
.formControlInvalid textarea {
  border-color: red;
  background: #ffd1d1;
}
